@use "base/variables" as v;
@use "base/mixins" as m;

.header{
    min-height: 60rem;
    background-image: url(../img/fondo.jpg),url(../img/fondo.jpg),linear-gradient(to right, rgba(v.$negro,1),rgba(v.$negro,0.5));
    border: 2px solid black;
    background-size: cover;
    &__contenedor{
        @include m.contenedor;
    }
    &__navegacion{
        padding:5rem 0;
        display:flex;
        justify-content: center;
        gap:2rem;
        @include m.tablet{
            justify-content:flex-end;
        }
    }

    &__enlace{
        @include m.enlace;
    }
    &__form{
        display: flex;
    }
    &__submit{
        @extend .header__enlace;
    }
    &__contenido{
        padding-top:10rem;
        @include m.tablet{
            padding-top: 5rem;
        }

    }
    &__logo{
        @include m.texto-fluido(1rem,8rem);
        @include m.texto-degradado(v.$azul,v.$secundario);
        text-align: center;
        @include m.tablet{
            text-align: left;
            display: inline-block;
        }
    }

    &__texto{
        font-size: 3rem;
        font-weight: v.$bold;
        color:v.$blanco;
        margin: 2rem 0;
        text-align: center;
        text-transform: uppercase;
        @include m.tablet{
            text-align: left;
        }
        &--modalidad{
            font-size: 2rem;
        }
    }

    &__boton{
        @include m.boton(v.$secundario,v.$blanco);
        text-transform: uppercase;
        margin:2rem 0;
        @include m.tablet{
            width: auto;
        }
    }

}